"use client"

import * as React from "react"
import {useLocale } from 'next-intl'
import { useRouter, usePathname } from 'next/navigation'
import Image from 'next/image'

function LanguageSwitcher() {
    const locale = useLocale()
    const router = useRouter()
    const pathname = usePathname()
    const [mounted, setMounted] = React.useState(false)

    React.useEffect(() => {
        setMounted(true)
    }, [])

    if (!mounted) return null

    const toggleLanguage = () => {
        const newLocale = locale === "en" ? "np" : "en"
        router.replace(pathname, { locale: newLocale })
    }

    return (
        <div className="mt-1">
            <button
                onClick={toggleLanguage}
                className="flex items-center p-2 rounded text-gray-700 dark:text-white transition"
                aria-label={locale === "en" ? "Switch to Nepali" : "Switch to English"}
            >
                {locale === "en" ? (
                    <div className="flex items-center">
                        <Image
                            src="/images/nepali.png"
                            alt="Nepali Flag"
                            className="transition-transform duration-200"
                            width={20}
                            height={20}
                        />
                        <span className="ml-2 text-sm">NP</span>
                    </div>
                ) : (
                    <div className="flex items-center">
                        <Image
                            src="/images/english.webp"
                            alt="English Flag"
                            className="transition-transform duration-200"
                            width={20}
                            height={20}
                        />
                        <span className="ml-2 text-sm">EN</span>
                    </div>
                )}
            </button>
        </div>
    )
}


export default LanguageSwitcher